import { SDK_URLS } from '../core/constants';
import { log, warn, error } from '../utils/dom';
/**
 * Live2D SDK 
 *  PIXI.jsCubism Corepixi-live2d-display 
 */
export const Live2DLoader = {
    _READY_MARK: '__desktopPetLive2dLoaderReady_v2',
    _RUNTIME_KEY: '__desktopPetLive2dRuntime_v2',
    _JSDELIVR_HOSTS: [
        'cdn.jsdelivr.net',
        'fastly.jsdelivr.net',
        'testingcf.jsdelivr.net',
        'gcore.jsdelivr.net',
    ],
    isLoaded: false,
    loadPromise: null,
    async load() {
        const top = window.parent ?? window;
        if (this.isLoaded) {
            const runtime = this._getRuntime(top);
            if (runtime)
                return true;
            warn(' Live2D runtime  SDK PIXI');
            this.isLoaded = false;
            this.loadPromise = null;
        }
        const runtime = this._getRuntime(top);
        if (runtime) {
            this.isLoaded = true;
            top[this._READY_MARK] = true;
            this.loadPromise = null;
            return true;
        }
        if (this.loadPromise)
            return this.loadPromise;
        this.loadPromise = this._doLoad();
        return this.loadPromise;
    },
    getRuntime(targetWindow) {
        const top = targetWindow ?? window.parent ?? window;
        return this._getRuntime(top);
    },
    getPixi(targetWindow) {
        return this.getRuntime(targetWindow)?.pixi ?? null;
    },
    _getRuntime(top) {
        const runtime = top[this._RUNTIME_KEY];
        if (!runtime || typeof runtime !== 'object')
            return null;
        const pixi = runtime.pixi;
        const live2dModel = runtime.live2dModel;
        if (!pixi || !live2dModel)
            return null;
        const pixiMajor = this._getPixiMajorVersion(pixi);
        if (pixiMajor !== 6)
            return null;
        // runtime  PIXI 
        if (top.PIXI !== pixi)
            return null;
        if (pixi?.live2d?.Live2DModel !== live2dModel)
            return null;
        return runtime;
    },
    _setRuntime(top) {
        const pixi = top.PIXI;
        const live2dModel = pixi?.live2d?.Live2DModel;
        const pixiMajor = this._getPixiMajorVersion(pixi);
        if (pixiMajor !== 6 || !live2dModel) {
            return null;
        }
        const runtime = {
            pixi,
            live2dModel,
            pixiVersion: String(pixi?.VERSION || 'unknown'),
            loadedAt: Date.now(),
        };
        top[this._RUNTIME_KEY] = runtime;
        return runtime;
    },
    async _doLoad() {
        const top = window.parent ?? window;
        try {
            const existingRuntime = this._getRuntime(top);
            if (existingRuntime) {
                this.isLoaded = true;
                top[this._READY_MARK] = true;
                this.loadPromise = null;
                return true;
            }
            //  PIXI.js
            const pixi = top.PIXI;
            const pixiMajor = this._getPixiMajorVersion(pixi);
            if (!pixi || pixiMajor !== 6) {
                if (pixi && pixiMajor !== null && pixiMajor !== 6) {
                    warn(' PIXI  PIXI v6', {
                        detected: pixi.VERSION ?? 'unknown',
                    });
                }
                log(' PIXI.js...');
                await this._loadScriptWithFallback(SDK_URLS.PIXI, top, 'PIXI.js');
                log('PIXI.js ');
            }
            if (!top.window.PIXI) {
                top.window.PIXI = top.PIXI;
            }
            //  Cubism 4 Core
            if (!this._hasValidCubism4Core(top)) {
                if (top.Live2DCubismCore) {
                    warn(' Cubism 4 Core ');
                }
                log(' Cubism 4 Core...');
                try {
                    await this._loadScriptWithFallback(SDK_URLS.CUBISM4_CORE, top, 'Cubism 4 Core', 20000);
                    log('Cubism 4 Core ');
                }
                catch (e) {
                    warn('Cubism 4 Core ', e);
                }
            }
            //  Cubism 2 Core
            if (!top.Live2D) {
                log(' Cubism 2 Core...');
                try {
                    await this._loadScriptWithFallback(SDK_URLS.CUBISM2_CORE, top, 'Cubism 2 Core', 20000);
                    log('Cubism 2 Core ');
                }
                catch (e) {
                    warn('Cubism 2 Core ', e);
                }
            }
            //  pixi-live2d-display
            log(' pixi-live2d-display...');
            await this._loadScriptWithFallback(SDK_URLS.PIXI_LIVE2D_DISPLAY, top, 'pixi-live2d-display', 25000);
            await new Promise(r => setTimeout(r, 120));
            const runtime = this._setRuntime(top);
            if (!runtime) {
                throw new Error('pixi-live2d-display ');
            }
            this.isLoaded = true;
            top[this._READY_MARK] = true;
            this.loadPromise = null;
            log('Live2D SDK ', {
                pixi: runtime.pixiVersion,
            });
            return true;
        }
        catch (e) {
            error('Live2D SDK :', e);
            top[this._READY_MARK] = false;
            top[this._RUNTIME_KEY] = null;
            this.isLoaded = false;
            this.loadPromise = null;
            return false;
        }
    },
    _getPixiMajorVersion(pixi) {
        if (!pixi || typeof pixi !== 'object')
            return null;
        const version = String(pixi.VERSION || '').trim();
        if (!version)
            return null;
        const majorStr = version.split('.')[0];
        const major = Number.parseInt(majorStr, 10);
        return Number.isFinite(major) ? major : null;
    },
    _buildUrlCandidates(originalUrl) {
        const url = String(originalUrl || '').trim();
        if (!url)
            return [];
        const candidates = [];
        const add = (candidate) => {
            const value = String(candidate || '').trim();
            if (!value)
                return;
            if (!candidates.includes(value))
                candidates.push(value);
        };
        add(url);
        let urlObj;
        try {
            urlObj = new URL(url);
        }
        catch {
            return candidates;
        }
        const host = urlObj.hostname.toLowerCase();
        if (host.endsWith('jsdelivr.net')) {
            for (const h of this._JSDELIVR_HOSTS) {
                try {
                    const u = new URL(url);
                    u.hostname = h;
                    add(u.toString());
                }
                catch {
                    // ignore
                }
            }
            if (urlObj.pathname.startsWith('/npm/')) {
                try {
                    const u = new URL(url);
                    u.hostname = 'unpkg.com';
                    u.pathname = urlObj.pathname.replace(/^\/npm\//, '/');
                    add(u.toString());
                }
                catch {
                    // ignore
                }
            }
            if (urlObj.pathname.startsWith('/gh/')) {
                const rest = urlObj.pathname.slice('/gh/'.length);
                const parts = rest.split('/').filter(Boolean);
                if (parts.length >= 3) {
                    const owner = parts[0];
                    const repoWithRef = parts[1];
                    const filePath = parts.slice(2).join('/');
                    if (owner && repoWithRef && filePath) {
                        const [repo, ref] = repoWithRef.split('@');
                        const suffix = `${urlObj.search || ''}${urlObj.hash || ''}`;
                        if (ref) {
                            add(`https://raw.githubusercontent.com/${owner}/${repo}/${ref}/${filePath}${suffix}`);
                        }
                        else {
                            add(`https://raw.githubusercontent.com/${owner}/${repo}/master/${filePath}${suffix}`);
                            add(`https://raw.githubusercontent.com/${owner}/${repo}/main/${filePath}${suffix}`);
                        }
                    }
                }
            }
        }
        return candidates;
    },
    _hasValidCubism4Core(targetWindow) {
        const core = targetWindow.Live2DCubismCore;
        if (!core || typeof core !== 'object')
            return false;
        const hasVersion = typeof core.Version === 'function';
        const hasMoc = typeof core.Moc === 'function' || typeof core.Moc === 'object';
        const hasUtils = typeof core.Utils === 'object';
        return hasVersion || hasMoc || hasUtils;
    },
    _loadScriptBySrc(url, targetWindow, timeoutMs = 15000) {
        return new Promise((resolve, reject) => {
            let timer = null;
            try {
                const doc = targetWindow.document;
                const script = doc.createElement('script');
                script.src = url;
                script.async = true;
                script.dataset.desktopPetSdk = 'true';
                const cleanup = () => {
                    script.onload = null;
                    script.onerror = null;
                    if (timer !== null) {
                        targetWindow.clearTimeout(timer);
                        timer = null;
                    }
                };
                script.onload = () => {
                    cleanup();
                    resolve();
                };
                script.onerror = () => {
                    cleanup();
                    reject(new Error(`: ${url}`));
                };
                timer = targetWindow.setTimeout(() => {
                    cleanup();
                    reject(new Error(` (${timeoutMs}ms): ${url}`));
                }, timeoutMs);
                doc.head.appendChild(script);
            }
            catch (e) {
                if (timer !== null) {
                    targetWindow.clearTimeout(timer);
                }
                reject(e);
            }
        });
    },
    async _loadScriptWithFallback(url, targetWindow, label, timeoutMs = 15000) {
        const candidates = this._buildUrlCandidates(url);
        let lastError = null;
        for (let i = 0; i < candidates.length; i++) {
            const candidate = candidates[i];
            try {
                if (i > 0) {
                    warn(`${label} :`, candidate);
                }
                await this._loadScriptBySrc(candidate, targetWindow, timeoutMs);
                return candidate;
            }
            catch (e) {
                lastError = e;
            }
        }
        throw (lastError instanceof Error ? lastError : new Error(`${label} `));
    },
};
