/**
 *  iframe  <head> 
 *  jQuery  Vue  DOM 
 */
export function teleport_style(): void {
  // /
  deteleport_style();
  $(`<div>`)
    .attr('script_id', getScriptId())
    .append($(`head > style`, document).clone())
    .appendTo('head');
}

/**
 * 
 */
export function deteleport_style(): void {
  $(`head > div[script_id="${getScriptId()}"]`).remove();
}
